

.cardscontainer {
  /* background-color: yellow; */
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
  width: min(85vw, 1000px);
  margin: 0 auto;
  justify-content: center;
  font-size: var(--fs-200);
  margin-top: 60px;
}


.cardscontainer > div:nth-of-type(odd) {
  background-color: white;
  aspect-ratio: 4/3;
  text-align: left;
  align-self: center;
  padding: 20px;
  box-shadow: 5px 10px 15px rgba(0, 0, 0, 0.1);
  position: relative;
  border-radius: 10px;
  overflow: hidden;
}

.cardscontainer > div:nth-child(2) {
  display: grid;
  gap: 20px;
  /* background-color: red; */
  

}
.cardscontainer > div:nth-child(2) > div {
  aspect-ratio: 4/3;
  background-color: white;
  padding: 20px;
  text-align: left;
  box-shadow: 5px 10px 15px rgba(0, 0, 0, 0.1);
  position: relative;
  border-radius: 10px;
  overflow: hidden;
}

.cardscontainer .img-container {
  position: absolute;
  bottom: 20px;
  right: 20px;
}

@media (min-width: 600px) {
  .cardscontainer{
    grid-template-columns: 1fr 1fr;
    padding: 0;
  }
  .cardscontainer > div:nth-child(2) {
    grid-row: span 2;
  }
}
@media (min-width: 900px) {
  .cardscontainer{
    grid-template-columns: 1fr 1fr 1fr;
  }
  .cardscontainer > div:nth-child(2) {
    grid-row: auto;
  }
}